<template>
  <b-modal ok-only size="xl" id="modalVariants" :title="' Invoice - ' + node.data.id">
    <div v-if="loading" class="text-center mt-5 mb-5">
      <i class="fa fa-circle-o-notch fa-spin fa-fw view__loader fa-lg"></i>
      <br />loading
    </div>

    <form v-if="!loading">
      <div class="row">
        <span class="col-12">
          <label for>Transaction ID (TXID)</label>
          <input type="text" v-model="node.data.txidIncoming" class="form-control" name />
        </span>

        <span class="col-12">
          <label for>Confirmation</label>
          <input type="number" v-model="node.data.confirmations" class="form-control" name />
        </span>

        <span class="col-12">
          <label for>Amount received</label>
          <input type="number" v-model="node.data.amount_received" class="form-control" name />
        </span>
      </div>
      <hr />
      <div class="row">
        <span class="col-12">
          <label for>Wallet</label>
          <input type="text" v-model="node.data.wallet" class="form-control" name disabled />
          <small>
            <a
              v-if="node.data.currency.code  === 13"
              target="_blank"
              :href="'https://live.blockcypher.com/ltc/address/' + node.data.wallet"
            >CHECK ON BLOCKCHAIN HERE!</a>
            <a
              v-if="node.data.currency.code  === 12"
              target="_blank"
              :href="'https://www.blockchain.com/btc/address/' + node.data.wallet"
            >CHECK ON BLOCKCHAIN HERE!</a>
          </small>
        </span>

        <span class="col-12 mt-3">
          <label for>
            <h2>
              Price:
              <span
                class="badge badge-danger"
              >{{node.data.total_crypto}} {{node.data.currency ? (node.data.currency.name) : (0) }}</span>
            </h2>
          </label>
        </span>

        <span class="col-12">
          <label for>
            <strong>Plan price: ${{node.data.plan && node.data.plan.price}} USD</strong>
          </label>
        </span>
      </div>
      <div v-if="node.data.blockchain_txs">
        <label for>Wallet transactions:</label>
        <div class="highlight">
          <vue-json-pretty highlightMouseoverNode :path="'res'" :data="node.data.blockchain_txs"></vue-json-pretty>
        </div>
      </div>
      <hr />
      <b-row>
        <b-form-group class="col-12" label-sr-only>
          <b-form-checkbox-group stacked>
            <div class="custom-control custom-checkbox">
              <input
                type="checkbox"
                class="custom-control-input"
                id="customChk1"
                v-model="node.data.asign_comissions"
              />
              <label class="custom-control-label" for="customChk1">Skip comissions</label>
            </div>
          </b-form-checkbox-group>
          <b-form-checkbox-group stacked>
            <div class="custom-control custom-checkbox">
              <input
                type="checkbox"
                class="custom-control-input"
                id="customChk12"
                v-model="node.data.corp_activation"
              />
              <label class="custom-control-label" for="customChk12">Corporative activation</label>
            </div>
          </b-form-checkbox-group>
          <b-form-checkbox-group stacked>
            <div class="custom-control custom-checkbox">
              <input
                type="checkbox"
                class="custom-control-input"
                id="customChk13"
                v-model="node.data.daily_disabled"
              />
              <label class="custom-control-label" for="customChk13">Disable daily bonus</label>
            </div>
          </b-form-checkbox-group>
          <b-form-checkbox-group stacked>
            <div class="custom-control custom-checkbox">
              <input
                type="checkbox"
                class="custom-control-input"
                id="customChk14"
                v-model="node.data.big_pro_account"
              />
              <label class="custom-control-label" for="customChk14">Pro subscription</label>
            </div>
          </b-form-checkbox-group>
        </b-form-group>
      </b-row>
    </form>

    <div slot="modal-footer">
      <button
        type="button"
        class="btn btn-dark mr-2"
        data-dismiss="modal"
        @click="hideModal()"
      >Close</button>
      <button
        type="button"
        v-if="node.data.invoice_status && node.data.invoice_status  === 'pending'"
        class="btn btn-success mr-1"
        @click.prevent="updateInvoice"
        :disabled="loading"
      >Confirm</button>
    </div>
  </b-modal>
</template>

<script>
import { serverBus } from "@/main";

import VueJsonPretty from "vue-json-pretty";

export default {
  name: "invoice-activation-modal",
  data() {
    return {
      loading: false,
      show: true,
      node: {
        delivery_attempt: 1,
        data: {
          txidIncoming: null,
          txid: null,
          status: Object,
          currency: Object,
          invoice_status: Object,
          asign_comissions: false,
          corp_activation: true,
          daily_disabled: true,
          big_pro_account: true
        }
      }
    };
  },
  watch: {
    "node.data.txidIncoming": function(newVal) {
      this.node.data.txid = `${this.$store.getters.currentUser.username}-${newVal}`;
    }
  },
  components: {
    VueJsonPretty
  },
  created() {
    serverBus.$on("invoices-modal-activation", (node) => {
      this.node.data = node;
      this.node.data.address = node.wallet;
      this.node.data.txidIncoming = node.txid;
      this.node.data.blockchain_txs = node.blockchain_txs && JSON.parse(node.blockchain_txs);
      this.node.data.asign_comissions = true;
      this.node.data.corp_activation = true;
      this.node.data.daily_disabled = true;
      this.node.data.big_pro_account = true;
      this.loading = false;
      this.showModal();
    });
  },
  beforeDestroy() {
    serverBus.$off("invoices-modal-activation");
  },
  methods: {
    showModal() {
      this.$root.$emit("bv::show::modal", "modalVariants");
    },
    hideModal() {
      this.$root.$emit("bv::hide::modal", "modalVariants");
    },
    updateInvoice() {
      this.loading = true;
      this.$http({
        url: "/api/v1/client/invoices/activation",
        data: this.node,
        method: "PUT",
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken
        }
      })
        .then(response => {
          this.$toasted.show("Invoice successfully updated", {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true
          });
          this.loading = false;
          this.hideModal();
          serverBus.$emit("invoices-fetch");
        })
        .catch(error => {
          this.loading = false;
          this.$toasted.show("Network error", {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true
          });
        });
    }
  }
};
</script>
